.a-chart {
  height: 505px;
  background: blue;
  background: #FFFFFF;
  border: 1px solid #000000;
  box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
  border-radius: 10px;

  &__labels {
    display: flex;
    margin-top: 36px;
    margin-left: 30px;

    div:nth-child(1) {
      margin-right: 30px;
    }
  }

  &__main {
    height: 368px;
    width: 852px;
    margin: auto;
    margin-top: 42px;
    justify-content: center;
    display: flex;

  }
}
